
import { defineComponent } from 'vue'
export default defineComponent({
	name: 'SpButton',
	props: {
		busy: {
			type: Boolean
		},
		link: {
			type: String
		},
		href: {
			type: String
		},
		target: {
			type: String
		},
		type: {
			type: String
		},
		disabled: {
			type: Boolean,
			default: false
		}
	}
})
