
import { defineComponent } from 'vue'
export default defineComponent({
	name: 'SpLinkIcon',
	props: {
		link: {
			type: String
		},
		href: {
			type: String
		},
		target: {
			type: String
		},
		icon: {
			type: String
		},
		text: {
			type: String,
			required: true
		}
	}
})
